<template>
  <div>
    <div class="bg-white border-bottom d-md-block d-none">
      <div class="d-flex align-items-center container justify-content-between py-3">
        <div class="d-flex align-items-center">
          <h2 class="first-letter-caps mb-1">Mocks</h2>
        </div>
      </div>
    </div>
    <div>
      <b-row v-if="mocks && mocks.length" class="ml-2">
        <b-col class="col-3">
          <b-row class="px-2 " v-for="(mock, index) in mocks" :key="index">
            <b-link class="btn btn-outline-light height-75 btn-block text-center text-dark show-event-radio pt-1 pb-2" @click="getMockData(mock)">
              <b-row class="justify-content-between px-3" v-if="mock.name">
                <span class="event-title mr-4 pr-6 pt-2 mt-1 text-truncate ">
                  {{ mock.name }}
                </span>
              </b-row>
            </b-link>
          </b-row>
        </b-col>
        <b-col class="col-9">
          <b-row v-if="mocks && mocks.length">
            <iframe v-if="src" :src="src" width="100%" height="750px"></iframe>
          </b-row>
        </b-col>
      </b-row>
      <b-row v-else class="align-items-center px-1 row">
        <div class="mx-auto py-5 text-center">
          <img src="/img/mockly/mocks.svg" width="300" class="my-4" />
          <h1 class="mb-2 text-black-50">No mocks Found</h1>
        </div>
      </b-row>
    </div>
  </div>
</template>
<script>
  import Mockly from "@/js/mockly/mockly";
  export default {
    props: ["project"],
    data() {
      return {
        domainId: window.domain,
        projectId: window.user,
        mockId: window.id,
        mocks: [],
        src: "",
      };
    },
    async mounted() {
      await this.getMocks();
    },

    methods: {
      async getMocks() {
        this.mocks = await Mockly.getMocks(this.domainId, this.projectId, this.$route.query.p, this.mockId, this.$route.query.m);
      },
      async getMockData(mock) {
        this.src = mock.url ? mock.url : "";
      },
    },
  };
</script>
